.author_photo_block {
    width: 50vh;
    height: 50vh;
    background: linear-gradient(transparent, white);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    position: relative;
    box-shadow: 1px 15px 26px -9px #ffffff;
}

.author_photo {
    width: 90%;
    height: 45vh;
    position: relative;

}

.author_photo:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgb(0, 0, 0, 0.2);
}

.author_photo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.closeModal {
    background: none;
    border: none;
    font-size: 25px;
    color: white;
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
}

.animate_container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.animate_container li {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.4);
    animation: animate 35s linear infinite;
    bottom: -150px;
}

.animate_container li:nth-child(1) {
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}

.animate_container li:nth-child(2) {
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.animate_container li:nth-child(3) {
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.animate_container li:nth-child(4) {
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.animate_container li:nth-child(5) {
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.animate_container li:nth-child(6) {
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7) {
    left: 25%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.animate_container li:nth-child(8) {
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.animate_container li:nth-child(9) {
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.animate_container li:nth-child(10) {
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}

@keyframes animate {

    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100% {
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}
@media only screen and (max-width: 894px) {
    .author_photo_block{
        width: 100%;
        height: 100%;
    }
    .author_photo{
        width: 98%;
        height: 100%;
    }
}
